/**
 * 推荐人填写暂存
 **/
import React, {useState} from 'react';
import Button from 'element-react/dist/npm/es5/src/button';
import Input from 'element-react/dist/npm/es5/src/input';
import {bindActionCreators} from 'redux';
import {connect} from 'react-redux';
import {contractAction, updateWalletInfo} from '../../actions';
import {cutOff, fillZero} from '../../Utils/utils';
import Message from "../Parts/Message/Message";
import './MsgBox.scss';
import {getTokenProfit} from "../../Utils/getProfit";


const Investment = (props) => {
  const [value, setValue] = useState('');
  const [loading, setLoading] = useState(false)
  const { tokenInfo, tokenBalance, tokenAmount, tokenPayoutsTo } = props;
  const tokenProfit = getTokenProfit(tokenAmount, tokenBalance, tokenPayoutsTo, tokenInfo.maxSupply);
  console.log(props, 'ssssdsdfsdfsdfsdfssss');

  const reinvest = () => {
    // props.updateUnformedUpstreamPlayer(value);
    console.log(value, tokenProfit, 'sdfsdfsdfsdffsf')
    console.log(value, '复投多少EOS');
    if (!value || value <= 0 || value > (tokenProfit || 0)) {
      Message('请输入有效数量', { type: 'error' });
      return;
    }

    setLoading(true);
    const TTC_REINVEST = 'TTC_REINVEST';
    props.updateWalletInfo(TTC_REINVEST, true);
    const { name } = props.identity || {};
    props.contractAction(props.contract[0], 'tinvest', {
      user: name,
      quantity: fillZero(value) + ' EOS'
    })
      .then(res => {
        Message('复投成功', { type: 'success' });
        props.updateWalletInfo(TTC_REINVEST, false);
        props.close();
        setValue('');
        setLoading(false);
      })
      .catch(err => {
        console.log(err, 'withdraw_fail');
        Message('复投失败', { type: 'error' });
        props.updateWalletInfo(TTC_REINVEST, false);
        props.close();
        setValue('');
        setLoading(false);
      });
  };

  const withdraw = () => {
    if (!value || value <= 0 || value > (tokenProfit || 0)) {
      Message('请输入有效数量', { type: 'error' });
      return;
    }
    setLoading(true);
    const WITH_TTC_DRAW = 'WITH_TTC_DRAW';
    updateWalletInfo(WITH_TTC_DRAW, true);
    console.log('sssssssssssss');
    const { name } = props.identity || {};
    console.log(444444, fillZero(value) + ' EOS');
    props.contractAction(props.contract[0], 'twithdraw', {
      user: name,
      quantity: fillZero(value) + ' EOS'
    })
      .then(res => {
        Message('提取成功', { type: 'success' });
        props.updateWalletInfo(WITH_TTC_DRAW, false);
        props.close();
        setValue('');
        setLoading(false);
      })
      .catch(err => {
        console.log(err, 'withdraw_fail');
        Message('提取失败', { type: 'error' });
        props.updateWalletInfo(WITH_TTC_DRAW, false);
        props.close();
        setValue('');
        setLoading(false);
      });
  };

  const withdrawGame = () => {
    const WITH_KEY_DRAW = 'WITH_KEY_DRAW';
    props.updateWalletInfo(WITH_KEY_DRAW, true);
    console.log(props.data, '444444');
    if (!value || value <= 0 || value > (props.data || 0)) {
      Message('请输入有效数量', { type: 'error' });
      return;
    }
    setLoading(true);
    console.log(fillZero(value) + ' EOS', 'sssssssss111sss')
    props.contractAction(props.contract[0], 'withdraw', {
      user: props.identity.name,
      quantity: fillZero(value) + ' EOS'
    })
      .then(res => {
        Message('提取成功', { type: 'success' });
        props.updateWalletInfo(WITH_KEY_DRAW, false);
        props.close();
        setValue('');
        setLoading(false);
      })
      .catch(err => {
        console.log(err, '提取失败');
        Message('提取失败', { type: 'error' });
        props.updateWalletInfo(WITH_KEY_DRAW, false);
        props.close();
        setValue('');
        setLoading(false);
      });
  };

  const confirm = ()  => { // modal 确认
    console.log(props, 'fbi');
    if (props.type === 'investment') {
      reinvest();
      // hlk 复购
      console.log('=====> hlk 复投')
    } else if (props.type === 'extract-hlk') {
      // hlk 取出分红
      console.log('=====> hlk 取出分红');
      withdraw();
    } else if (props.type === 'extract-game') {
      // game 取出分红
      console.log('=====> game 取出分红');
      withdrawGame();
    }
  };

  return (
    <div className="modal-content-up-bind">
      <i className="el-icon-close modal-btn-close" onClick={props.close} />
      {
        props.type === 'investment' ?
          <p className="modal-up-bind-title">
            复购 <img src={require('../../images/key_white.png')} alt="endless60" />
          </p> :
          <p className="modal-up-bind-title">
            提取分红
          </p>
      }
      <Input
        type='number'
        value={value}
        onChange={setValue}
      />
      <p className="modal-up-bind-babel">
        可{props.type === 'investment' ? '用' : '提取'}数量:
        {
        props.type === 'investment' ||  props.type === 'extract-hlk' ?
        cutOff(tokenProfit, 4) || 0 : props.data
      } EOS
      </p>
      <Button
        className="modal-up-bind-cancel"
        onClick={() => props.close(true)}
      >
        取消
      </Button>
      <Button
        className="modal-up-bind-save"
        onClick={confirm}
        loading={loading}
      >
        确认
      </Button>
    </div>
  )
};

function mapStateToProps(state) {
  return {
    language: state.language,
    contract: state.contract,
    roundInfo: state.roundInfo || {},
    identity: state.identity,
    tokenBalance: parseFloat(state.tokenInfo.balance),
    tokenPayoutsTo: parseFloat(state.tokenInfo.payoutsTo),
    tokenAmount: state.roundInfo.tokenAmount,
    roundStatus: state.roundInfo.status,
    tokenInfo: state.tokenInfo,
    tokenSymbol: state.tokenInfo.symbol,
    walletInfo: state.walletInfo,
  };
}

function mapDispatchToProps(dispatch) {
  return bindActionCreators({contractAction, updateWalletInfo}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Investment);

