/**
 * 红利计算
 **/
import BigNumber from 'bignumber.js';
import {key2EOS} from "./key2EOS";
import {min2EOS} from "./unitConvert";
import {formatEOS} from "./utils";
import _compact from "lodash/compact";
import _filter from "lodash/filter";

/**
 * key红利
 * rnd.mask * p.prd / base - p.mask
 */
export function getKeyProfit(roundMask, playerMask, playerKeys, base) {
  // console.log(roundMask, playerMask, playerKeys, base, 9999999)
  return new BigNumber(roundMask).div(base).times(playerKeys).minus(playerMask).toNumber()
}

/**
 * token红利
 * 分红值 / 1000w * balance - payoutsTo_
 * tokenBalance 非最小单位
 */
export function getTokenProfit(tokenAmount, tokenBalance, payoutsTo, base) {
  console.log(new BigNumber(tokenAmount).div(base).times(tokenBalance).minus(payoutsTo).div(10000).toNumber(), 'jjjjjjj')
  return new BigNumber(tokenAmount).div(base).times(tokenBalance).minus(payoutsTo).div(10000).toNumber()
}

/**
 * 开奖后达成红利
 * 奖池 x 0.15 / 所有解锁玩家下超过500key的玩家数 * 我的超过500key的下游玩家数
 */
export function getEstimateBonusProfit(pot, allQualifiedPlayers = 0, isUnlocked) {
  const allEstimateProfit = new BigNumber(pot).div(10000).times(0.15);

  // 还没人达成，我达成就都归我了
  if (!parseFloat(allQualifiedPlayers) || allQualifiedPlayers === 0) return allEstimateProfit.toNumber();

  // 有人达成，按我达成后比例分成
  const quantity = Math.max(2, isUnlocked || 0);
  const sAllQualifiedPlayers = isUnlocked ? allQualifiedPlayers : new BigNumber(allQualifiedPlayers).plus(quantity);
  return allEstimateProfit.div(sAllQualifiedPlayers).times(quantity).toNumber()
}

/**
 * 游戏总分红
 */
export function getGameProfit(eos) {
  return eos * 0.55;
}

/**
 * 直推奖励
 * 0人达标：
 *  本轮我花费的EOS * 0.15 + (当前购买500key所需EOS * 0.05 * 2)
 * 1人达标：
 *  达标人的返利 + 本轮我花费的EOS * 0.15 + (当前购买500key所需EOS * 0.05）
 * 超过1人达标：
 *  本轮我花费的EOS * 0.15 + 所有达标人的返利总和
 */
export function getEstimateAffProfit(playerEOS, roundKeys, downstreamList) {

  // 我的解锁下游玩家
  const myUnlockedDownstream = _compact(
    _filter(downstreamList, function(d) {
      return d.keys >= 500 * 10 && d.name !== ''
    })
  );

  // 500Key花费的5%
  const thresholdEOS = (key2EOS(roundKeys, 500) || 0) * 0.05;

  // 我的花费返利
  const myRebate = min2EOS(playerEOS) * 0.15;

  // 达标人的返利总和
  const getAllAffProfit = (list) => {
    let value =  new BigNumber(0);
    list.forEach((d) => {
      if (d.profit) {
        value = value.plus(d.profit || 0);
      }
    });
    return min2EOS(value)
  };

  let allAffProfit = getAllAffProfit(myUnlockedDownstream);

  // 没有推荐人达标
  if (myUnlockedDownstream.length === 0) {
    return formatEOS(new BigNumber(myRebate).plus(thresholdEOS * 2));
  }

  // 1个达标人
  if (myUnlockedDownstream.length <= 1) {
    return formatEOS(new BigNumber(myRebate).plus(thresholdEOS).plus(allAffProfit));
  }

  // 达标人数 >= 2
  return formatEOS(new BigNumber(myRebate).plus(allAffProfit));
}

